import React, { useContext, useState } from 'react';

const emptyUser = {};
const UserContext = React.createContext(emptyUser);

function Demo() {
  const [user, setUser] = useState(emptyUser);

  const onLogin = () => {
    setUser({ name: 'Mike' });
  };

  const onLogout = () => {
    setUser(emptyUser);
  };

  return (
    // UserContext.Provider æä¾› context å€¼
    <UserContext.Provider value={user}>
      <UserLogin onLogin={onLogin} onLogout={onLogout} />
      <UserCard />
    </UserContext.Provider>
  );
}

function UserLogin(props) {
  // è·å– UserContext å½“å‰çš„å€¼ï¼Œç±»ä¼¼äº <UserContext.Consumer />
  const user = useContext(UserContext);
  return (
    <div>
      {user.name ? (
        <button onClick={props.onLogout}>Logout</button>
      ) : (
        <button onClick={props.onLogin}>Login</button>
      )}
    </div>
  );
}

function UserCard() {
  // è·å– UserContext å½“å‰çš„å€¼ï¼Œç±»ä¼¼äº <UserContext.Consumer />
  const user = useContext(UserContext);
  return <div>{user.name ? `Hi: ${user.name}` : 'Your are not login'}</div>;
}

export default Demo;

