import React, {
  useState,
  useRef,
  useImperativeHandle,
  forwardRef,
} from 'react';

// ç¬¬äºŒä¸ª ref å‚æ•°é€šè¿‡ forwardRef è½¬å‘è¿›æ¥
const Add = (props, ref) => {
  // ä½¿ç”¨åˆå§‹å€¼æ¥ä½œä¸ºå†…éƒ¨çš„ state
  const [count, setCount] = useState(props.initialCount);

  const onStateAdd = () => setCount(count + 1);

  // æš´éœ²å¯¹åº”çš„æ–¹æ³•
  useImperativeHandle(
    ref,
    () => ({
      reset: () => {
        setCount(props.initialCount);
      },
    }),
    []
  );

  return (
    <div>
      <div>count: {count}</div>
      <button onClick={onStateAdd}>Add Count State</button>
    </div>
  );
};

// è°ƒç”¨ AddWithRef èƒ½å°† ref ä¼ é€’ä¸‹å»
const AddWithRef = forwardRef(Add);

const Demo = () => {
  const addRef = useRef(null);
  const reset = () => addRef.current.reset();

  return (
    <>
      <AddWithRef ref={addRef} initialCount={10} />
      <button onClick={reset}>Reset</button>
    </>
  );
};

export default Demo;
