import React, { useState, useRef, useEffect, useLayoutEffect } from 'react';

const getStyle = background => {
  return {
    background,
    color: '#fff',
    fontWeight: 'bold',
    display: 'block',
    textAlign: 'center',
    position: 'relative',
    lineHeight: '140px',
    width: 140,
    height: 140,
    transition: 'all 1s',
  };
};

const Box = () => {
  const eleA = useRef(null);
  const eleB = useRef(null);

  // useEffect åœ¨æ¸²æŸ“ DOM å‰ã€å¼‚æ­¥ã€‘æ‰§è¡Œè®¾ç½®æ ·å¼
  // æ‰€ä»¥å…ˆæ¸²æŸ“å¥½ DOMï¼Œå†è®¾ç½®æ ·å¼ï¼Œä¼šæœ‰æœ‰åŠ¨ç”»æ•ˆæœ
  useEffect(() => {
    eleA.current.style.transform = 'translateX(300px)';
  }, []);

  // useLayoutEffect åœ¨æ¸²æŸ“ DOM å‰ã€åŒæ­¥ã€‘æ‰§è¡Œè®¾ç½®æ ·å¼
  // æ‰€ä»¥ä¼šé˜»å¡æ¸²æŸ“ï¼Œç­‰è®¾ç½®å¥½æ ·å¼åå†æ¸²æŸ“DOMï¼Œä¸ä¼šæœ‰åŠ¨ç”»æ•ˆæœ
  useLayoutEffect(() => {
    eleB.current.style.transform = 'translateX(300px)';
  }, []);

  return (
    <div>
      <div style={getStyle('blue')} ref={eleA}>
        useEffect
      </div>
      <div style={getStyle('red')} ref={eleB}>
        useLayoutEffect
      </div>
    </div>
  );
};

const Demo = () => {
  const [count, setCount] = useState(0);
  return (
    <div>
      <button onClick={() => setCount(count + 1)}>Refresh</button>
      <Box key={count} />
    </div>
  );
};

export default Demo;
