import React, { useState, useReducer, useRef, useEffect } from 'react';

/**
 * éå—æ§è¡¨å•
 */
const FormV1 = () => {
  const nameRef = useRef(null);
  const phoneRef = useRef(null);
  const [values, setValues] = useState({});
  const onGetValues = () => {
    const name = nameRef.current.value;
    const phone = phoneRef.current.value;
    setValues({ name, phone });
  };
  return (
    <form>
      <p>
        <label>Name: </label>
        <input type="text" ref={nameRef} />
      </p>
      <p>
        <label>Phone: </label>
        <input type="text" ref={phoneRef} />
      </p>
      <button onClick={onGetValues}>Get Values</button>
      {/* Set Form */}
      <p>Form Values: {JSON.stringify(values)}</p>
    </form>
  );
};

/**
 * é€šè¿‡åŸºç¡€ state hook ç®¡ç†è¡¨å•
 */
const FormV2 = () => {
  const [name, setName] = useState();
  const [phone, setPhone] = useState();
  return (
    <form>
      <p>
        <label>Name: </label>
        <input
          type="text"
          onChange={e => setName(e.target.value)}
          value={name || ''}
        />
      </p>
      <p>
        <label>Phone: </label>
        <input
          type="text"
          onChange={e => setPhone(e.target.value)}
          value={phone || ''}
        />
      </p>
      <p>Form Values: {JSON.stringify({ name, phone })}</p>
    </form>
  );
};

/**
 * é€šè¿‡ reducer ç»Ÿä¸€ç®¡ç†è¡¨å•
 */
const formReducer = (state, action) => {
  return {
    ...state,
    ...action,
  };
};
const FormV3 = () => {
  const [values, dispatch] = useReducer(formReducer, {});
  return (
    <form>
      <p>
        <label>Name: </label>
        <input
          type="text"
          onChange={e => dispatch({ name: e.target.value })}
          value={values.name || ''}
        />
      </p>
      <p>
        <label>Phone: </label>
        <input
          type="text"
          onChange={e => dispatch({ phone: e.target.value })}
          value={values.phone || ''}
        />
      </p>
      <p>Form Values: {JSON.stringify(values)}</p>
    </form>
  );
};

/**
 * æå–è‡ªå®šä¹‰ Hookï¼šUseForm
 */
export const useForm = () => {
  const formReducer = (state, action) => ({ ...state, ...action });
  const [values, dispatch] = useReducer(formReducer, {});
  const getFieldProps = (field, options) => {
    return {
      type: 'input',
      value: values[field] || '',
      onChange: e => {
        dispatch({ [field]: e.target.value });
        setTimeout(() => {
          options && options.onChange && options.onChange();
        });
      },
    };
  };

  const setFieldsValue = field => dispatch(field);

  return [values, getFieldProps, setFieldsValue];
};
const FormV4 = () => {
  const [values, getFieldProps] = useForm({});
  return (
    <form>
      <p>
        <label>Name: </label>
        <input {...getFieldProps('name')} />
      </p>
      <p>
        <label>Phone: </label>
        <input {...getFieldProps('phone')} />
      </p>
      <p>Form Values: {JSON.stringify(values)}</p>
    </form>
  );
};

const Demo = () => {
  return (
    <ul>
      <li>
        <h3>V1 éå—æ§è¡¨å•</h3>
        <FormV1 />
      </li>
      <li>
        <h3>V2 useState å—æ§è¡¨å•</h3>
        <FormV2 />
      </li>
      <li>
        <h3>V3 useReducer ç»Ÿä¸€ç®¡ç†</h3>
        <FormV3 />
      </li>
      <li>
        <h3>V4 useForm è‡ªå®šä¹‰ Hook</h3>
        <FormV4 />
      </li>
    </ul>
  );
};

export default Demo;
